<template>
<div class="input">
    <label :for="name">
        <div class="label-text">{{ labelText }}</div>
        <input
          v-validate="validationRules"
          class="input"
          :type="type"
          :name="name"
          :id="name"
          :placeholder="placeholder"
          autocomplete="off" />
          <span class="active" />
    </label>
    <span :class="{'cross': true, 'is-danger': errors.has(name)}" />
</div>
</template>

<script>
export default {
  name: 'CombinedInput',
  props: {
    placeholder: String,
    type: String,
    labelText: String,
    name: String,
    validationRules: {
      type: Object,
      default: () => ({
        required: true,
      }),
    },
  },
};
</script>

<style lang="scss" scoped>
.input {
  position: relative;

  .cross {
    position: absolute;
    right: 20px;
    top: 0;
    transform: translate(0, 50%);
    pointer-events: none;
    z-index: 50;
    width: 40px;
    height: 40px;

    &.is-danger {
      &::before,
      &::after {
        background-color: rgb(244, 67, 54);
      }
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      width: 14px;
      height: 2px;
      top: 50%;
      left: 50%;
    }

    &::before {
      transform: rotateZ(45deg);
    }

    &::after {
      transform: rotateZ(-45deg);
    }
  }
}

label {
  cursor: pointer;
  width: 100%;
  position: relative;
  margin-bottom: 0;
  display: block;
  z-index: 11;
}

.active {
  position: absolute;
  height: 100%;
  width: 4px;
  left: 0;
  top: 0;
  transition: all 0.2s ease-in-out;
}

.label-text {
  display: inline-block;
  width: auto;
  z-index: 10;
  position: absolute;
  top: 20px;
  left: 30px;
  font-size: 11px;
  color: rgba(5, 5, 5, 0.3);
}

input {
  box-sizing: border-box;
  height: 78px;
  display: block;
  outline: 0;
  border: 0;
  width: 100%;
  box-shadow: 0px 14px 21px 0 rgba(0, 15, 255, 0.04);
  border-top: solid 1px #eaeaea;
  border-left: solid 1px #eaeaea;
  border-right: solid 1px #eaeaea;
  padding-top: 40px;
  padding-left: 30px;
  padding-bottom: 21px;
  padding-right: 30px;
  color: black;
  font-size: 15px;
  font-weight: bold;
}

.input:last-child input {
  border-bottom: solid 1px #eaeaea;
}

input::placeholder {
  color: rgba(5, 5, 5, 0.4);
}

input:focus ~ .active {
  background-color: #0b20ff;
}
</style>



